@import "../styles/variables.scss";

/*#region mixins */
@mixin clearfix() {
    &:before, &:after {
        content: " ";
        display: table;
    }

    &:after {
        clear: both;
    }
}
@mixin disabled() {
    pointer-events: none;
    cursor: default;
    filter: alpha(opacity=8);
    box-shadow: none;
    opacity: .75;

    * {
        pointer-events: none;
    }
}

@mixin rotate($angle, $important: false) {
    -webkit-transform: rotate($angle) if($important, #{'!important'}, null);
    transform: rotate($angle) if($important, #{'!important'}, null);
}

@mixin box-shadow() {
    -webkit-box-shadow: 0px 3px 8px rgba($color-primary-extra,.03) !important;
    box-shadow: 0px 3px 8px rgba($color-primary-extra,.03) !important;
}

@mixin placeholder() {
    &::placeholder {
        @content;
    }

    &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
        @content;
    }

    &::-moz-placeholder { /* Firefox 19+ */
        @content;
    }

    &:-ms-input-placeholder { /* IE 10+ */
        @content;
    }

    &:-moz-placeholder { /* Firefox 18- */
        @content;
    }
}

@mixin flexbox {
 display: -webkit-box;
 display: -webkit-flex;
 display: -moz-flex;
 display: -ms-flexbox;
 display: flex;
}
/*#endregion */

/*#region classes */
.disabled, [disabled] {
    @include disabled();
}

.readonly, [readonly] {
    pointer-events: none;
    cursor: default;
    box-shadow: none;

    a, button {
        filter: alpha(opacity=8);
        opacity: .8;
    }
}

.clearfix {
    @include clearfix();
}

.hidden {
    display: none !important;
}

.invisible {
    visibility: hidden !important;
}

.center-block {
    display: block !important;
    margin-right: auto !important;
    margin-left: auto !important;
}

